#skills {
    font-family: var(--font-family);
    color: var(--color-text);
    margin: 2rem;
}

#skills-container {
    position: relative;
    display: flex;
    overflow-x: auto;
    width: 100%;
    padding: 1rem;
    scrollbar-width: thin;
    scrollbar-color: var(--color-btn-sc) var(--color-bg);
    scroll-behavior: smooth;
    user-select: none;
}

#skills-container::-webkit-scrollbar {
    width: 1rem;
    border-radius: .5rem;
    position: absolute;
    top: 0;
    right: 0;
}

#skills-container::-webkit-scrollbar-track {
    background-color: var(--color-bg);
}

#skills-container::-webkit-scrollbar-thumb {
    background-color: var(--color-btn-pr);
    border-radius: .5rem;
}

#skills-container::-webkit-scrollbar-thumb:hover {
    background-color: var(--color-btn-pr-dr);
}

.language-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 2rem
}

.bar-graph {
    width: 1.5rem;
    height: 10rem;
    background-color: var(--color-text);
    display: flex;
    align-items: flex-end;
    justify-content: center;
    position: relative;
    border-radius: .75rem;
}

.bar {
    width: 100%;
    background-color: var(--color-btn-sc);
    border-radius: .75rem;
}

.coming-soon {
    position: absolute;
    bottom: .8rem;
    left: 0;
    right: .1rem;
    text-align: center;
    font-size: 1.2rem;
    transform: rotate(-90deg);
    color: var(--color-bg);
    font-weight: 800;
}


.language-icon {
    margin-top: 1rem;
    font-size: 3rem;
}

@media (max-width: 678px) {
    .language-item {
        margin: 0 .75rem;
    }
}